.btn {
  border: none;
  line-height: 1;
  padding: 0 40px;
  overflow: hidden;
  position: relative;
  color: #fff;
  transition: 0.4s cubic-bezier(0.25, 1, 0.5, 1);
  border: 2px solid transparent;
  border-radius: 8px;
}
.btn span {
  position: relative;
  display: inline-block;
  transition: 0.4s cubic-bezier(0.25, 1, 0.5, 1);
}
.btn span small {
  display: block;
  padding: 16px 0;
  font-size: 18px;
  font-weight: 600;
  line-height: 24px;
  transition: 0.4s cubic-bezier(0.25, 1, 0.5, 1);
}
.btn span small:nth-child(2) {
  left: 50%;
  top: 100%;
  opacity: 0;
  width: 100%;
  position: absolute;
  transform: translateX(-50%);
}
.btn:focus {
  outline: 0;
  box-shadow: none;
}
.btn:hover {
  z-index: 1;
  color: #fff;
  transform: translateY(-4px);
  box-shadow: 0 8px 24px 0 rgba(16, 18, 19, 0.4);
}
.btn:hover span small {
  color: #fff;
}
.btn:hover span small:nth-child(1) {
  opacity: 0;
  transform: translateY(-100%);
}
.btn:hover span small:nth-child(2) {
  top: 50%;
  opacity: 1;
  transform: translate(-50%, -50%);
}
.btn.btn_small {
  padding: 0 28px;
}
.btn.btn_small span small {
  padding: 9px 0;
}
.btn_primary {
  color: #035ab2;
  border-color: #fdca07;
  background-color: #fdca07;
}
.btn_primary:hover {
  color: #035ab2;
  box-shadow: 0 8px 24px 0 rgba(247, 255, 98, 0.4);
}
.btn_primary:hover span small {
  color: #035ab2;
}
.btn_dark {
  color: #fff;
  border-color: #035ab2;
  background-color: #035ab2;
}
.btn_dark:hover {
  color: #fff;
}
.btn_dark:hover span small {
  color: #fff;
}
.btn_warning {
  color: #035ab2;
  border-color: #ffd32b;
  background-color: #ffd32b;
}
.btn_warning:hover {
  color: #035ab2;
  box-shadow: 0 8px 24px 0 rgba(255, 211, 43, 0.5);
}
.btn_warning:hover span small {
  color: #035ab2;
}
.btn.border_dark {
  color: #035ab2;
  border-color: #035ab2;
}
.btn.border_dark:hover {
  color: #fff;
  background-color: #035ab2;
}
.btn.border_dark:hover span small {
  color: #fff;
}
.btn_unfill {
  font-size: 18px;
  font-weight: 600;
  line-height: 24px;
  color: #035ab2;
  align-items: center;
  display: inline-flex;
  font-family: Barlow, sans-serif;
}
.btn_unfill .btn_icon {
  width: 24px;
  height: 24px;
  overflow: hidden;
  margin-left: 10px;
  position: relative;
  border-radius: 100%;
  transition: 0.4s cubic-bezier(0.25, 1, 0.5, 1);
  background-color: #035ab2;
}
.btn_unfill .btn_icon i {
  top: 50%;
  font-size: 14px;
  position: absolute;
  color: #fdca07;
  transition: 0.4s cubic-bezier(0.25, 1, 0.5, 1);
}
.btn_unfill .btn_icon i:nth-child(1) {
  left: 50%;
  transform: translate(-50%, -50%);
}
.btn_unfill .btn_icon i:nth-child(2) {
  left: -100%;
  transform: translateY(-50%);
}
.btn_unfill:hover {
  color: #035ab2;
}
.btn_unfill:hover span {
  text-decoration: underline;
}
.btn_unfill:hover .btn_icon {
  transform: translateX(2px);
}
.btn_unfill:hover .btn_icon i:nth-child(1) {
  left: 100%;
  transform: translateY(-50%);
}
.btn_unfill:hover .btn_icon i:nth-child(2) {
  left: 50%;
  transform: translate(-50%, -50%);
}
