@keyframes mdp-float-aura {
  from {
    box-shadow: 0 0 0 -1px var(--hmp-color-75), 0 0 0 -1px var(--hmp-color-50),
      0 0 0 -1px var(--hmp-color-25);
  }
  to {
    box-shadow: 0 0 0 15px rgba(255, 255, 255, 0),
      0 0 0 30px rgba(255, 255, 255, 0), 0 0 0 45px rgba(255, 255, 255, 0);
  }
}
@keyframes mdp-float-init {
  0% {
    opacity: 0;
    transform: scale(0.25);
  }
  50% {
    opacity: 0;
  }
  80% {
    transform: scale(1);
    opacity: 1;
  }
  90% {
    transform: scale(1.2);
  }
  100% {
    transform: scale(1);
  }
}
.mdp-float {
  --hmp-color: rgb(51, 153, 255);
  --hmp-color-75: rgba(51, 153, 255, 0.75);
  --hmp-color-50: rgba(51, 153, 255, 0.5);
  --hmp-color-25: rgba(51, 153, 255, 0.25);
  --hmp-color-5: rgba(51, 153, 255, 0.05);
  position: absolute;
  z-index: 999999;
  border-radius: 50%;
  top: 3em;
  right: 3em;
  width: 3em;
  height: 3em;
  transition: 0.75s;
  transition-timing-function: ease-out;
}
.mdp-float:hover {
  animation: mdp-float-aura infinite;
  animation-duration: 2s;
}
.mdp-float.mdp-float-inertia {
  transition: 0.25s;
}
.mdp-float.mdp-float-slow {
  transition-delay: 0.5s;
  transition: 5s;
}
.mdp-float.mdp-float-init {
  animation: mdp-float-init 1s;
  animation-fill-mode: both;
}
.mdp-float-btn {
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 50%;
  width: 3em;
  height: 3em;
  text-shadow: 1px 0 0 var(--hmp-color);
  background-image: linear-gradient(
    210deg,
    var(--hmp-color-5) 0,
    var(--hmp-color-25) 100%
  );
  background-color: #fff;
  transition: 0.25s;
  box-shadow: 0 7px 13px -3px var(--hmp-color-25),
    0 2px 4px 0 var(--hmp-color-50), inset 0 0 0 0 var(--hmp-color);
  position: relative;
}
.mdp-float-btn:before {
  content: "";
  position: absolute;
  z-index: 1;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: var(--hmp-color);
  border-radius: 50%;
  transform: scale(0);
  transition-property: transform;
  transition-duration: 0.25s;
  transition-timing-function: ease-out;
}
.mdp-float-btn svg {
  width: 1em;
  height: 1em;
  fill: var(--hmp-color);
  z-index: 2;
}
.mdp-float-btn:focus {
  outline: 0;
}
.mdp-float-btn:hover {
  transition: 0.25s;
  transition-delay: 125ms;
  box-shadow: 0 7px 26px -3px var(--hmp-color-25),
    0 2px 8px 0 var(--hmp-color-50);
  border: none;
}
.mdp-float-btn:hover:before {
  transform: scale(1);
}
.mdp-float-btn:hover svg {
  transform: scale(1.1);
  fill: #fff;
}
.mdp-float-btn:active {
  transform: scale(0.75);
}
@media (max-width: 720px) {
  .mdp-float {
    top: 1.5em;
    right: 1.5em;
  }
}
.cb-cursor {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 999;
  contain: layout style size;
  pointer-events: none;
  will-change: transform;
  transition: opacity 0.3s, color 0.4s;
}
.cb-cursor:before {
  content: "";
  position: absolute;
  top: -24px;
  left: -24px;
  display: block;
  width: 48px;
  height: 48px;
  transform: scale(0);
  background: #035ab2;
  border-radius: 50%;
  transition: transform 0.3s ease-in-out, opacity 0.1s;
}
.cb-cursor-text {
  position: absolute;
  top: -18px;
  left: -18px;
  width: 36px;
  height: 36px;
  display: flex;
  align-items: center;
  justify-content: center;
  transform: scale(0) rotate(10deg);
  opacity: 0;
  color: #fff;
  font-size: 16px;
  line-height: 20px;
  text-align: center;
  letter-spacing: -0.01em;
  transition: opacity 0.4s, transform 0.3s;
}
@supports (mix-blend-mode: exclusion) {
  .cb-cursor.-exclusion,
  .cb-cursor.-opaque {
    mix-blend-mode: exclusion;
  }
}
@supports (mix-blend-mode: exclusion) {
  .cb-cursor.-exclusion:before,
  .cb-cursor.-opaque:before {
    background: #fff;
  }
}
.cb-cursor.-normal,
.cb-cursor.-text {
  mix-blend-mode: normal;
}
.cb-cursor.-normal:before,
.cb-cursor.-text:before {
  background: rgba(0, 0, 0, 0.9);
}
.cb-cursor.-inverse {
  color: #fff;
}
.cb-cursor.-visible:before {
  transform: scale(0.2);
}
.cb-cursor.-visible.-active:before {
  transform: scale(0.23);
  transition-duration: 0.2s;
}
.cb-cursor.-pointer:before {
  transform: scale(0.15);
}
.cb-cursor.-text:before {
  opacity: 0.85;
  transform: scale(1.7);
}
.cb-cursor.-text .cb-cursor-text {
  opacity: 1;
  transform: scale(1);
}
.cb-cursor.-text.-active:before {
  transform: scale(1.6);
  transition-duration: 0.2s;
}
.cb-cursor.-opaque:before {
  transform: scale(1.32);
}
.cb-cursor.-opaque.-active:before {
  transform: scale(1.2);
}
.cb-cursor.-lg:before {
  transform: scale(2);
}
.cb-cursor.-hidden:before {
  transform: scale(0);
}
.-cursor-default {
  color: #3a10e5;
}
