  background-color: rgba(255, 255, 255, 0.96);
  -webkit-backdrop-filter: saturate(180%) blur(20px);
  backdrop-filter: saturate(180%) blur(20px);
  box-shadow: 0 2px 6px 0 rgba(16, 18, 19, 0.08);
  /* transition: background-color 0.3s ease-in, padding 0.3s; */
}
.main_menu {
  padding: 0;
}
.main_menu_list > li {
  margin: 0 1px;
  line-height: 1;
  font-size: 16px;
}
.main_menu_list > li > a {
  z-index: 1;
  font-size: 17px;
  font-weight: 500;
  line-height: 23px;
  color: #035ab2;
  position: relative;
  align-items: center;
  display: inline-flex;
  padding: 9px 20px 8px;
  font-family: Roboto, sans-serif;
  border-radius: 8px;
}
.main_menu_list > li.active > a,
.main_menu_list > li:hover > a {
  color: #035ab2;
  background-color: #f6f6f6;
}
.main_menu_list > li:hover > a:after {
  transform: rotateX(-180deg);
}
.main_menu_list .dropdown > a {
  position: relative;
}
.main_menu_list .dropdown > a:after {
  float: right;
  line-height: 1;
  font-size: 14px;
  content: "\f107";
  font-weight: 400;
  margin-top: 2px;
  margin-left: 5px;
  transition: 0.4s cubic-bezier(0.25, 1, 0.5, 1);
  font-family: "Font Awesome 5 Pro";
}
.main_menu_list .dropdown-menu {
  top: 100%;
  display: none;
  margin: 2px 0 0;
  padding: 14px 0;
  animation: unset;
  min-width: 210px;
  inset: 100% auto auto auto;
  border-radius: 8px;
  border: 2px solid #035ab2;
  box-shadow: -6px 6px 0 0 #101213;
}
.main_menu_list .dropdown-menu:before {
  top: -4px;
  height: 2px;
}
.main_menu_list .dropdown-menu > li:not(:last-child) {
  margin-bottom: 1px;
}
.main_menu_list .dropdown-menu > li > a {
  display: block;
  line-height: 1;
  font-size: 16px;
  font-weight: 500;
  color: #29281e;
  position: relative;
  white-space: nowrap;
  transition: 0.4s cubic-bezier(0.25, 1, 0.5, 1);
  padding: 14px 30px 12px 32px;
}
.main_menu_list .dropdown-menu > li > a:before {
  top: 19px;
  left: 20px;
  width: 4px;
  height: 4px;
  content: "";
  opacity: 0.7;
  position: absolute;
  border-radius: 100%;
  background-color: #035ab2;
}
.main_menu_list .dropdown-menu > li.active > a,
.main_menu_list .dropdown-menu > li:hover > a {
  background-color: #fdca07;
}
.main_menu_list .dropdown-menu .dropdown > a:after {
  margin-top: 1px;
  content: "\f105";
}
.main_menu_list .dropdown-menu .dropdown-menu {
  top: 0;
  margin: 0;
  left: 100%;
}
.main_menu_list .dropdown-menu.show {
  display: block;
}
.main_menu_list .dropdown-menu.show > li {
  animation: 0.2s dropDown;
}
