.vanilla-calendar-day__popup {
  position: absolute;
  z-index: 2;
  left: 50%;
  top: 100%;
  transform: translateX(-50%);
  width: 130px;
  padding: 16px 12px 8px;
  color: #000;
  font-size: 12px;
  line-height: 14px;
  font-weight: 400;
  opacity: 0;
  pointer-events: none;
}
.vanilla-calendar-day__popup:hover {
  opacity: 1;
  pointer-events: auto;
}
.vanilla-calendar-day__popup::before {
  content: "";
  position: absolute;
  z-index: -1;
  left: 0;
  bottom: 0;
  width: 100%;
  height: calc(100% - 8px);
  display: block;
  border-radius: 4px;
  background-color: #fff;
  box-shadow: 0 2px 20px rgba(0, 0, 0, 0.1);
}
.vanilla-calendar-day__popup::after {
  content: "";
  position: absolute;
  z-index: -1;
  left: 50%;
  transform: translateX(-50%);
  top: 2px;
  display: block;
  height: 0;
  width: 0;
  border-color: transparent transparent #fff;
  border-style: solid;
  border-width: 0 10px 8px;
}
